.errorMessage {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 25px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fce4e4;
  border: 1px solid #fcc2c3;
  float: left;
  animation: fade 5s;
  animation-delay: 5s;
}

.errorText {
  color: #cc0033;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 13px;
  font-weight: bold;
  line-height: 20px;
  text-shadow: 1px 1px rgba(250, 250, 250, .3);
}

@keyframes fade {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}