.mapContainer {
  float: left;
  width: 100%;
  height: 100vh;
  .addControl {
    cursor: pointer;
    position: absolute;
    z-index: 666;
    top: 90px;
    left: 5px;
    width: 50px;
    height: 50px;
    border: 5px solid #2f2f2f;
    border-radius: 50%;
    color: #626161;

    &:hover {
      background-color: #f5f5f5;
      color: #2b2a29;
    }
    &:disabled {
      opacity: .4;
    }

    .addSign {
      font-weight: 600;
      font-size: 15px;
    }
  }
  .saveControl, .loadControl {
    display: flex;
    align-items: center;
    justify-content: center;
    top: 150px;
    background-color: #f5f5f5;
  }
  .loadControl {
    top: 210px;
  }
}


